* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  background-color: #907cff;
  border: none;
  border-radius: 20px;
  padding: 10px 40px;
  font-size: 18px;
}

button:focus {
  background-color: #aa55fa;
  box-shadow: 2px 2px 2px 1px #6a6a6d33;
}

input {
  margin-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 20px;
}

span {
  font-size: 20px;
  font-style: italic;
  font-weight: bold;
}

.timer {
  display: flex;
}

.field {
  padding-right: 10px;
}

